var test = require('ava');
var app = require('../app');
var request = require('supertest-promised')(app);

test(function (t) {
  return request.post('/')
    .send({
      "host": "197.211.242.121",
      "user": "admin",
      "pass": "admin"
    })
    .expect(200)
    .expect(function (res) {
      console.log(res.body);
      t.truthy(res.body);
    });
});
